const getters = {
    isLogin: state => state.user.isLogin,                                                       //
    email: state => state.user.userInfo.sEmail,                                                 //
    uid: state => state.user.userInfo.iUserId,                                                  //UID
    cellphone: state => state.user.userInfo.sCellphone,                                         //
    sSMSAreaCode: state => state.user.userInfo.sSMSAreaCode,                                    //
    lastLoginTime: state => state.user.userInfo.iLastLoginTime,                                 //
    lastLoginIPAddr: state => state.user.userInfo.sLastLoginIPAddr,                             //IP
    isHasEmail: state => state.user.userInfo.bIsHasEmail,                                       //
    isHasGoogleSecretKey: state => state.user.userInfo.bIsHasGoogleSecretKey,                   //Google
    switchCellphoneVerify: state => state.user.userInfo.bSwitchCellphoneVerify,                 //
    switchEmailVerify: state => state.user.userInfo.bSwitchEmailVerify,                         //
    switchGoogleVerify: state => state.user.userInfo.bSwitchGoogleVerify,                       //Google
    userCountry: state => state.user.userCountry,                                               //
    listenSmsStatus: state => state.user.listenSmsStatus,                                       //

    needMfa: state => state.user.needMfa,                                                       //

    iCreditLevel: state => state.user.stUserExtenInfo.iCreditLevel,                             //
    userName: state => state.user.stUserExtenInfo.sNickName,                                    //
    sAvatar: state => state.user.stUserExtenInfo.sAvatar,                                       //
    userType: state => state.user.stUserExtenInfo.iUserType,                                    //   3 1 2
    iCertificateStatus: state => state.user.stUserExtenInfo.iCertificateStatus,                 //
    iCertificateId: state => state.user.stUserExtenInfo.iCertificateId,                         //ID
    iLiveCertificateStatus: state => state.user.stUserExtenInfo.iLiveCertificateStatus,         //
    iLiveCertificateId: state => state.user.stUserExtenInfo.iLiveCertificateId,                 //ID
    iAdsStatus: state => state.user.stUserExtenInfo.iAdsStatus,                                 //
    iDrawalStatus: state => state.user.stUserExtenInfo.iDrawalStatus,                           //
    iMerchantStatus: state => state.user.stUserExtenInfo.iMerchantStatus,                       //
    iIsChangeNameStatus: state => state.user.stUserExtenInfo.iIsChangeNameStatus,               //
    iTakingOrder: state => state.user.stUserExtenInfo.iTakingOrder,                             //


    platform: state => state.app.platform,                                                      //
    version: state => state.app.version,                                                        //
    buildid: state => state.app.buildid,                                                        //

    fiatQuote: state => state.udc.fiatQuote,                                                    //
    commodityList: state => state.otc.commodityList,                                            //OTC
    getOrderCount: state => state.user.orderCount,                                              //
    getOrderState: state => state.user.orderState,                                              //
    iDepositAmount: state => state.user.stUserExtenInfo.iDepositAmount,                         //
    isWsLogin: state => state.websocket.isWsLogin,                                              //WS
    getAdsGradeList: state => state.otc.adsConfigList,                                          //
    getAdsGradeVal: (state) => (iLevel) => {                                                    //
        if(state.otc.adsConfigList){
            const data = state.otc.adsConfigList.find(i=>i.iLevel === iLevel)
            return data||{}
        }
    },
    getMarketdPrice: (state) => (sToken) => {                                                   //TokenOTC
        if(state.otc.marketPrice){
            const index = state.otc.marketPrice.findIndex(i=>i.sToken === sToken)
            return index > -1 ? Number(state.otc.marketPrice[index].dPrice) : 0
        }
    },
    mailUnreadCount: state => state.user.mailUnreadCount,                                       //
    isNetwork: state => state.app.network,                                                      //

}

export default getters
