let heartCheck = {
    timeout: 540000,        //9
    timeoutObj: null,
    serverTimeoutObj: null,
    reset: function(){
        clearTimeout(this.timeoutObj);
        clearTimeout(this.serverTimeoutObj);
        return this;
    },
    start: function(){
        let self = this;
        this.timeoutObj = setTimeout(function(){
            //
            //onmessage
            ws.send("ping");
            console.log("ping!----")
            self.serverTimeoutObj = setTimeout(function(){//
                ws.close();     //onclosereconnectws.close().reconnect onclose
            }, self.timeout)
        }, this.timeout)
    }
}

export default websocket