/**
 * CSScss
 * @param css css
 * @param id styleid
 */
export function cssExpand(css, id) {
  let style = document.createElement('style')
  style.type = "text/css"
  style.innerHTML = `@charset "UTF-8"; ${css}`
  // 
  if (id) {
    let $style = document.getElementById(id)
    if ($style != null) $style.outerHTML = ''
    style.id = id
  }
  // 
  document.head.appendChild(style)
}


/** jsJS */
// options 
//                         
//    vm             VueComponent    vue
//    event          Object          event
//    jsCode         String          js
//    errorMessage   String          
export function jsExpand(options = {}) {

  // windowkeyName
  let windowKeyName = 'J_CLICK_EVENT_OPTIONS'
  if (typeof window[windowKeyName] != 'object') {
    window[windowKeyName] = {}
  }

  // JSid
  let id = randomString(16, 'qwertyuioplkjhgfdsazxcvbnm'.toUpperCase())
  // 
  let code = `
    (function (o_${id}) {
      try {
        (function (globalEvent, vm) {
          ${options.jsCode}
        })(o_${id}.event, o_${id}.vm)
      } catch (e) {
        o_${id}.error(e)
      }
      o_${id}.done()
    })(window['${windowKeyName}']['EVENT_${id}'])
  `
  // script
  const script = document.createElement('script')
  // window
  window[windowKeyName]['EVENT_' + id] = {
    vm: options.vm,
    event: options.event,
    // 
    done() {
      //  script 
      script.outerHTML = ''
      delete window[windowKeyName]['EVENT_' + id]
    },
    // js
    error(e) {
      console.group(`${options.errorMessage || 'JS'}${new Date()}`)
      console.error(e)
      console.groupEnd()
    }
  }
  // document
  script.innerHTML = code
  document.body.appendChild(script)
}


/**
 * 
 *
 * 
 * { validator: (rule, value, callback) => validateDuplicateValue('sys_fill_rule', 'rule_code', value, this.model.id, callback) }
 *
 * @param tableName 
 * @param fieldName 
 * @param fieldVal 
 * @param dataId ID
 * @param callback
 */
export function validateDuplicateValue(tableName, fieldName, fieldVal, dataId, callback) {
  if (fieldVal) {
    let params = { tableName, fieldName, fieldVal, dataId }
    api.duplicateCheck(params).then(res => {
      res['success'] ? callback() : callback(res['message'])
    }).catch(err => {
      callback(err.message || err)
    })
  } else {
    callback()
  }
}

/**
 * code
 *
 * 
 * { validator: (rule, value, callback) => validateCheckRule('common', value, callback) }
 *
 * @param ruleCode  code
 * @param value 
 * @param callback
 */
export function validateCheckRule(ruleCode, value, callback) {
  if (ruleCode && value) {
    value = encodeURIComponent(value)
    api.checkRuleByCode({ ruleCode, value }).then(res => {
      res['success'] ? callback() : callback(res['message'])
    }).catch(err => {
      callback(err.message || err)
    })
  } else {
    callback()
  }
}

/**
 *  push 
 * @param array 
 * @param value 
 * @param key  id
 * @returns {boolean}  push  true false
 */
export function pushIfNotExist(array, value, key) {
  for (let item of array) {
    if (key && (item[key] === value[key])) {
      return false
    } else if (item === value) {
      return false
    }
  }
  array.push(value)
  return true
}

/**
 * 
 * @type {symbol}
 */
export const succeedSymbol = Symbol()
/**
 * 
 * @type {symbol}
 */
export const failedSymbol = Symbol()

/**
 *  promise  resolvePromisePromise
 *  Promise.all 
 *
 * @param promise PromisePromise
 * @returns {Promise<any>}
 */
export function alwaysResolve(promise) {
  return new Promise((resolve, reject) => {
    let p = promise
    if (typeof promise === 'function') {
      p = promise()
    }
    if (p instanceof Promise) {
      p.then(data => {
        resolve({ type: succeedSymbol, data })
      }).catch(error => {
        resolve({ type: failedSymbol, error })
      })
    } else {
      reject('alwaysResolve: PromisePromise')
    }
  })
}

/**
 * 
 *
 * (debounce)(delay)100ms
 * 100ms
 * 
 *
 * @param fn 
 * @param delay 
 * @returns {Function}
 */
export function simpleDebounce(fn, delay = 100) {
  let timer = null
  return function () {
    let args = arguments
    if (timer) {
      clearTimeout(timer)
    }
    timer = setTimeout(() => {
      fn.apply(this, args)
    }, delay)
  }
}

/**
 * 
 * @param text 
 * @param checker  
 * @param replacer 
 * @returns {String} 
 */
export function replaceAll(text, checker, replacer) {
  let lastText = text
  text = text.replace(checker, replacer)
  if (lastText !== text) {
    return replaceAll(text, checker, replacer)
  }
  return text
}

/**
 *  IE11EdgeChromeFirefoxSafari
 * JEditableTable Span
 */
export function getEventPath(event) {
  let target = event.target
  let path = (event.composedPath && event.composedPath()) || event.path

  if (path != null) {
    return (path.indexOf(window) < 0) ? path.concat(window) : path
  }

  if (target === window) {
    return [window]
  }

  let getParents = (node, memo) => {
    memo = memo || []
    const parentNode = node.parentNode

    if (!parentNode) {
      return memo
    } else {
      return getParents(parentNode, memo.concat(parentNode))
    }
  }
  return [target].concat(getParents(target), window)
}

/**
 * 
 * @param vm
 * @param name
 * @returns {Vue | null|null|Vue}
 */
export function getVmParentByName(vm, name) {
  let parent = vm.$parent
  if (parent && parent.$options) {
    if (parent.$options.name === name) {
      return parent
    } else {
      let res = getVmParentByName(parent, name)
      if (res) {
        return res
      }
    }
  }
  return null
}

/**
 * null | undefined
 *
 * @param value 
 * @param def valuenull | undefined''
 */
export function neverNull(value, def) {
  return value == null ? (neverNull(def, '')) : value
}



/**
 * 
 *
 * @param curV 
 * @param reqV 
 */
export function compVersion(curV, reqV) {
  var arr1 = curV.toString().split('.');
  var arr2 = reqV.toString().split('.');
  //
  var minL = Math.min(arr1.length, arr2.length);
  var pos = 0; //
  var diff = 0; //
  var flag = false;
  //
  while (pos < minL) {
    diff = parseInt(arr1[pos]) - parseInt(arr2[pos]);
    if (diff == 0) {
      pos++;
      continue;
    } else if (diff > 0) {
      flag = true;
      break;
    } else {
      flag = false;
      break;
    }
  }
  return flag;
}
