import Vue from 'vue'
import { postAction } from './index'
const baseUrl = process.env.VUE_APP_AUTH_API_URL
const authUrl = {
    CheckRegistered:'/v1/auth/CheckRegistered',             //
    SendSMSVerifyCode:'/v1/auth/SendSMSVerifyCode',         //
    UserRegister:'/v1/auth/UserRegister',                   //
    UserLogin:'/v1/auth/UserLogin',                         //
    RefreshSessionKey:'/v1/auth/RefreshSessionKey',         //SessionKey
    UnifySecurityVerify:'/v1/auth/UnifySecurityVerify',     //
    SecurityLogin:'/v1/auth/SecurityLogin',                 //
    QueryUserInfo:'/v1/auth/QueryUserInfo',                 //
    QueryLoginHistory:'/v1/auth/QueryLoginHistory',         //
    SwitchLoginVerify:'/v1/auth/SwitchLoginVerify',         ///ActionSecurityLogin
    // UnifySecurityVerify:'/v1/auth/UnifySecurityVerify',  //
    GetVerifyMethod:'/v1/auth/GetVerifyMethod',             //
    ChangePassword:'/v1/auth/ChangePassword',               //
    RetrievePassword:'/v1/auth/RetrievePassword',           //
    ResetPassword:'/v1/auth/ResetPassword',                 //
    CellphoneVerify:'/v1/auth/CellphoneVerify',             //
    SwitchCellphoneVerify:'/v1/auth/SwitchCellphoneVerify', ///
    CheckVerifyCode:'/v1/auth/CheckVerifyCode',             ///
    EmailVerify:'/v1/auth/EmailVerify',                     //
    SetAntiFishingCode:'/v1/auth/SetAntiFishingCode',       //SetingAntiFishingCode
    SendEmailVerifyCode:'/v1/auth/SendEmailVerifyCode',     //
    GetGoogleSecretKey:'/v1/auth/GetGoogleSecretKey',       //
    GoogleVerify:'/v1/auth/GoogleVerify',                   //
    SwitchGoogleVerify:'/v1/auth/SwitchGoogleVerify',       ///
    VerifyPassword:'/v1/auth/VerifyPassword',               //
    SwitchEmailVerify:'/v1/auth/SwitchEmailVerify',         ///
    RetrieveVerify:'/v1/auth/RetrieveVerify',               //
}

for(let k in authUrl) {
    authUrl[k] = baseUrl + authUrl[k]
}

const getSMSVerifyCode = (params)=>postAction(authUrl.SendSMSVerifyCode,params);                //
const checkRegistered = (params)=>postAction(authUrl.CheckRegistered,params);                   //
const userRegister = (params)=>postAction(authUrl.UserRegister,params);                         //
const login = (params)=>postAction(authUrl.UserLogin,params);                                   //
const refreshSessionKey = (params)=>postAction(authUrl.RefreshSessionKey,params);               //SessionKey
const unifySecurityVerify = (params)=>postAction(authUrl.UnifySecurityVerify,params);           //
const securityLogin = (params)=>postAction(authUrl.SecurityLogin,params);                       //
const queryUserInfo = (params)=>postAction(authUrl.QueryUserInfo,params);                       //
const queryLoginHistory = (params)=>postAction(authUrl.QueryLoginHistory,params);               //
const switchLoginVerify = (params)=>postAction(authUrl.SwitchLoginVerify,params);               ///ActionSecurityLogin
const getVerifyMethod = (params)=>postAction(authUrl.GetVerifyMethod,params);                   //
const changePassword = (params)=>postAction(authUrl.ChangePassword,params);                     //
const retrievePassword = (params)=>postAction(authUrl.RetrievePassword,params);                 //
const resetPassword = (params)=>postAction(authUrl.ResetPassword,params);                       //
const cellphoneVerify = (params)=>postAction(authUrl.CellphoneVerify,params);                   //
const switchCellphoneVerify = (params)=>postAction(authUrl.SwitchCellphoneVerify,params);       ///
const checkVerifyCode = (params)=>postAction(authUrl.CheckVerifyCode,params);                   ///
const emailVerify = (params)=>postAction(authUrl.EmailVerify,params);                           //
const setAntiFishingCode = (params)=>postAction(authUrl.SetAntiFishingCode,params);             //SetingAntiFishingCode
const sendEmailVerifyCode = (params)=>postAction(authUrl.SendEmailVerifyCode,params);           //
const getGoogleSecretKey = (params)=>postAction(authUrl.GetGoogleSecretKey,params);             //
const googleVerify = (params)=>postAction(authUrl.GoogleVerify,params);                         //
const switchGoogleVerify = (params)=>postAction(authUrl.SwitchGoogleVerify,params);             ///
const verifyPassword = (params)=>postAction(authUrl.VerifyPassword,params);                     //
const switchEmailVerify = (params)=>postAction(authUrl.SwitchEmailVerify,params);               ///
const retrieveVerify = (params)=>postAction(authUrl.RetrieveVerify,params);                     //


export {
    authUrl,
    getSMSVerifyCode,
    checkRegistered,
    userRegister,
    login,
    refreshSessionKey,
    unifySecurityVerify,
    securityLogin,
    queryUserInfo,
    queryLoginHistory,
    switchLoginVerify,
    getVerifyMethod,
    changePassword,
    retrievePassword,
    resetPassword,
    cellphoneVerify,
    switchCellphoneVerify,
    checkVerifyCode,
    emailVerify,
    setAntiFishingCode,
    sendEmailVerifyCode,
    getGoogleSecretKey,
    googleVerify,
    switchGoogleVerify,
    verifyPassword,
    switchEmailVerify,
    retrieveVerify,
}